@import url(https://fonts.googleapis.com/css2?family=Baloo+Paaji+2:wght@500&display=swap);
*{padding:0;margin:0;border:0;box-sizing:border-box;font-family:"Baloo Paaji 2", monospace}body{max-width:800px;margin:0 auto;padding:0 1rem;background:#18191a}body header{display:flex;align-items:center;justify-content:space-between;margin-top:1rem;color:whitesmoke;background-color:blue}body header .user{display:flex;align-items:center}body header .user .user-img{margin-right:1.5rem;border-radius:50%;display:flex;justify-content:center;align-items:center;border:2px solid whitesmoke}body header .user .user-img img{border-radius:50%;width:3.5rem;height:3.5rem}body header .user .user-text{line-height:1;font-size:1.5rem}body header .notifications{font-size:1.5rem;display:flex;justify-content:center;align-items:center}

